.input {
width: 50%;
padding: 12px 20px;
margin: 8px 0;
display: inline-block;
border: 1px solid #ccc;
box-sizing: border-box;
}

.button {
background-color: #b563ff;
color: white;
padding: 14px 20px;
margin: 8px 0;
border: none;
cursor: pointer;
width: 12%;
text-align: center;
margin-left: 210px;
}

.button:hover {
transform: scale(1.2);
}

.cancelbtn {
width: auto;
padding: 10px 18px;
background-color: #f44336;
}

.suaxoa{
  padding: 10px;
}

.suaxoa:hover{
  background-color: #b563ff;
  color: white;
  transform: scale(1.1);
}

.as1 {
  padding: 15px;
}
.as2 {
  box-shadow: 0 1px 3px 0 rgba(0,0,0,.1), 0 1px 2px 0 rgba(0,0,0,.06);
}

.as2 {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 0 solid rgba(0,0,0,.125);
  border-radius: .25rem;
}

.as1 {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1rem;
}

.smss {
  margin-right: -8px;
  margin-left: -8px;
}

.smss>.col, .smss>[class*=col-] {
  padding-right: 8px;
  padding-left: 8px;
}
.mb-3, .my-3 {
  margin-bottom: 1rem!important;
}

.bg-gray-300 {
  background-color: #e2e8f0;
}
.h-100 {
  height: 100%!important;
}
.shadow-none {
  box-shadow: none!important;
}